.footer {
  padding: 40px 0;
  &__inner {
    text-align: center;
  }
  &__tel {
    font-size: 15px;
    line-height: 120%;
    color: #000;
  }
  &__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
  }
  &__social {
    &:not(:first-child) {
      margin-left: 10px;
    }
  }
  &__desc {
    font-size: 15px;
    line-height: 120%;
    margin-top: 30px;
  }
}