.header {
  &-top {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    padding: 5px 0;
    background: #eff1f2;
    border-bottom: 3px solid #a0138e;
    &__link {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      &:hover {
        color: #000;
        .header-top__btn {
          background: #a0138e;
          color: #fff;
        }
      }
    }
    &__text {
      font-size: 14px;
      line-height: 130%;
      span {
        font-weight: 700;
      }
    }
    &__btn {
      width: 115px;
      height: 37px;
      background: transparent;
      border: 1px solid #a0138e;
      border-radius: 17px;
      color: #a0138e;
      margin-left: 7px;
    }
  }
  &-bottom {
    padding: 70px 0 20px 0;
    background: #fff;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__right {
      display: flex;
      align-items: center;
    }
    &__logo {
      display: flex;
      align-items: flex-end;
      a {
        width: 94px;
        img {
          width: 100%;
          height: auto;
        }
      }
      span {
        font-size: 16px;
        line-height: 180%;
        display: block;
        color: #000;
        margin-left: 15px;
      }
    }
    &__list {
      display: flex;
      align-items: center;
    }
    &__link {
      font-size: 16px;
      line-height: 120%;
      color: #000;
      &:not(:first-child) {
        margin-left: 25px;
      }
      &--active {
        color: #d32bb7;
      }
    }
    &__burger {
      position: relative;
      z-index: 5;
      cursor: pointer;
      width: 35px;
      height: 20px;
      display: none;
      span {
        position: absolute;
        background: #D32BB7;
        transition: all 0.5s;
        &:nth-child(1) {
          top: 0;
          left: 0;
          width: 100%;
          height: 2px;
        }
        &:nth-child(2) {
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 100%;
          height: 2px;
        }
        &:nth-child(3) {
          left: 0;
          bottom: 0;
          width: 100%;
          height: 2px;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .header {
    &-top {
     &__text {
       display: none;
     }
    }
    &-bottom {
      &__burger {
        display: block;
        &--active {
          span {
            background: #fff;
            &:nth-child(1) {
              transform: rotate(-45deg);
              top: 50%;
            }
            &:nth-child(2) {
              display: none;
            }
            &:nth-child(3) {
              transform: rotate(45deg);
              top: 50%;
            }
          }
        }
      }
      &__list {
        position: absolute;
        z-index: 4;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100vh;
        flex-direction: column;
        background: #d32bb7;
        padding: 130px 15px 30px;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
      }
      &__link {
        color: #fff;
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
        a {
          &:hover {
            color: #cbcbcb;
          }
        }
        &--active {
          color: #cbcbcb;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &-bottom {
      padding: 60px 0 10px;
      &__logo {
        a {
          width: 60px;
        }
        span {
          display: none;
        }
      }
    }
  }
}