.display2 {
  font-family: Poppins, sans-serif;
  font-size: 40px;
  line-height: 130%;
  font-weight: 700;
}
.form {
  &-group {
    border: none;
    margin: 0;
    padding: 0;
    span {
      font-size: 15px;
      line-height: 140%;
      color: #fff;
      display: flex;
    }
    input {
      font-size: 15px;
      line-height: 120%;
      padding: 0 5px;
      background: transparent;
      border: 1px solid #fff;
      border-top: 0px;
      border-left: 0px;
      border-right: 0px;
      color: #fff;
      &::placeholder {
        color: #fff;
      }
      &:hover {
        border-top: 1px solid #fff;
        border-left: 1px solid #fff;
        border-right: 1px solid #fff;
      }
      &:focus {
        border-top: 1px solid #fff;
        border-left: 1px solid #fff;
        border-right: 1px solid #fff;
      }
    }
  }
  &-textarea {
    font-size: 15px;
    line-height: 120%;
    padding: 7px;
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
    resize: none;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    &::placeholder {
      color: #fff;
    }
    &:focus {
      border-top: 1px solid #fff;
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
    }
    &:hover {
      border-top: 1px solid #fff;
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
    }
  }
  &-btn {
    font-size: 18px;
    line-height: 120%;
    background: #ffffff;
    color: #000;
    &:hover {
      background: #bfbfbf;
      color: #fff;
    }
  }
}
.main {
  padding: 100px 0 0;
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #fff;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(121, 121, 121, 0.5);
  }
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 820px;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    font-family: Poppins, sans-serif;
    font-size: 72px;
    line-height: 130%;
    font-weight: 700;
  }
  &__subtitle {
    font-family: times-new-roman, sans-serif;
    font-size: 20px;
    line-height: 130%;
    margin-top: 30px;
  }
  &__desc {
    font-size: 15px;
    line-height: 160%;
    margin-top: 30px;
  }
  &__tel {
    position: relative;
    z-index: 2;
    background: rgba(236, 236, 236, 0.4);
    padding: 50px 0;
    margin: 60px 0 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__number {
    width: 205px;
    height: 45px;
    background: transparent;
    border: 3px solid #000;
    color: #000;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background: #000;
      color: #fff;
    }
  }
}
.about {
  padding: 60px 0 30px;
  color: #fff;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 50%;
    background: #d32bb7;
    margin:0 5px 0 70px;
    padding: 140px 15px 100px;
  }
  &__right {
    width: 50%;
    margin:0 70px 0 5px;
    background-image: url("../img/about.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &__desc {
    max-width: 460px;
    margin: 0 auto;
    text-align: center;
  }
  &__text {
    font-size: 15px;
    line-height: 180%;
    margin-top: 30px;
  }
}
.team {
  padding: 30px 0 60px;
  &__inner {
    max-width: 980px;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    color: #d32bb7;
  }
  &__btn {
    width: 240px;
    height: 45px;
    background: transparent;
    font-size: 16px;
    border: 3px solid #d32bb7;
    color: #d32bb7;
    margin: 60px auto 0;
    &:hover {
      background: #d32bb7;
      color: #fff;
    }
  }
  &__items {
    margin-top: 120px;
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 60px;
    }
    &-left {
      width: 460px;
      flex-shrink: 0;
      margin-right: 60px;
    }
    &-right {
      width: calc(100% - 520px);
      img {
        width: 100%;
        height: auto;
      }
    }
    &-title {
      font-family: Poppins, sans-serif;
      font-size: 22px;
      line-height: 120%;
      font-weight: 700;
      color: #d32bb7;
    }
    &-subtitle {
      font-family: times-new-roman, sans-serif;
      font-size: 18px;
      line-height: 120%;
      color: #000;
      margin-top: 30px;
      color: #000;
    }
    &-text {
      font-size: 15px;
      line-height: 180%;
      margin-top: 30px;
      color: #000;
    }
  }
  &__btn2 {
    width: 162px;
    height: 45px;
    background: transparent;
    font-size: 16px;
    border: 3px solid #d32bb7;
    color: #d32bb7;
    margin: 100px auto 0;
    &:hover {
      background: #d32bb7;
      color: #fff;
    }
  }
}
.opportunities {
  background: #d32bb7;
  padding: 60px 0;
  &__inner {
    max-width: 940px;
    margin: 0 auto;
    text-align: center;
  }
  &__title {
    font-family: Poppins, sans-serif;
    color: #fff;
  }
  &__text {
    font-family: times-new-roman, sans-serif;
    font-size: 18px;
    line-height: 180%;
    color: #fff;
    margin-top: 30px;
  }
}
.services {
  padding: 150px 0;
  &__slider {
    max-width: 940px;
    margin: 0 auto;
    position: relative;
  }
  .swiper-slide {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-slide {
    &__left {
      width: calc(100% - 490px);
      img {
        width: 100%;
        height: auto;
      }
    }
    &__right {
      width: 460px;
      margin-left: 30px;
      text-align: end;
    }
    &__title {
      font-family: Poppins, sans-serif;
      font-size: 28px;
      line-height: 120%;
      font-weight: 700;
      color: #d32bb7;
    }
    &__subtitle {
      font-family: times-new-roman, sans-serif;
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      margin-top: 40px;
      color: #000;
    }
    &__text {
      font-family: times-new-roman, sans-serif;
      font-size: 18px;
      line-height: 180%;
      margin-top: 25px;
      color: #000;
    }
  }
  .swiper-button-next {
    right: -70px;
    transform: rotate(180deg);
    &:after {
      display: none;
    }
  }
  .swiper-button-prev {
    left: -70px;
    &:after {
      display: none;
    }
  }
  .swiper-pagination {
    bottom: -70px;
    display: flex;
    justify-content: center;
    align-items: center;
    &-bullet {
      width: 6px;
      height: 6px;
      background: #000;
      opacity: 1;
      &-active {
        width: 9px;
        height: 9px;
        background: transparent;
        border: 1px solid #000;
      }
    }
  }
}
.map {
  height: 470px;
  #map {
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
}
.contacts {
  background: #d32bb7;
  padding: 60px 0 100px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 50%;
    text-align: center;
  }
  &__right {
    width: 50%;
    margin-left: 30px;
  }
  &__title {
    color: #fff;
  }
  &__city {
    color: #fff;
    margin-top: 30px;
  }
  &__mail {
    color: #fff;
    margin-top: 30px;
    display: block;
    &:hover {
      color: #cbcbcb;
    }
  }
  &__tel {
    color: #fff;
    margin-top: 30px;
    display: block;
    &:hover {
      color: #cbcbcb;
    }
  }
  &-form {
    &__top {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 15px;
    }
    &__bottom {
      margin-top: 15px;
      .contacts-form__group {
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__group {
      span {
        padding-left: 5px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
    &__textarea {
      width: 100%;
      height: 115px;
    }
    &__btn {
      width: 100%;
      height: 50px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .about {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      margin: 0;
    }
    &__right {
     display: none;
    }
  }
}

@media screen and (max-width: 1150px) {
  .services {
    .swiper-slide__btns {
      display: flex;
      align-items: center;
      margin-top: 60px;
    }
    .swiper-button-prev {
      position: static;
      svg {
        width: 15px;
        height: auto;
      }
    }
    .swiper-button-next {
      position: static;
      margin-left: 15px;
      svg {
        width: 15px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .team {
    &__inner {
      max-width: 100%;
    }
    &__item {
      flex-direction: column;
      max-width: 500px;
      margin: 0 auto;
      &-left {
        width: 100%;
        margin: 30px 0 0 0;
      }
      &-right {
        width: 100%;
        order: -1;
      }
     }
  }
  .services {
    padding: 60px 0;
    .swiper-slide {
      flex-direction: column;
    }
    &-slide {
      &__left {
        width: 100%;
        max-width: 500px;
      }
      &__right {
        width: 100%;
        margin: 30px 0 0 0;
        text-align: center;
      }
    }
    .swiper-pagination {
      bottom: -20px;
    }
  }
  .contacts {
    &__left {
      width: 40%;
    }
    &__right {
      width: 60%;
    }
  }
}

@media screen and (max-width: 850px) {
  .contacts {
    &__inner {
      display: block;
      max-width: 600px;
      margin: 0 auto;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 50px 0 0 0;
    }
    &-form {
      &__btn {
        width: 300px;
        margin: 0 auto;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 30px;
  }
  .main {
    padding: 60px 0 0 0;
    &__title {
      font-size: 35px;
    }
    &__subtitle {
      font-size: 18px;
      margin-top: 20px;
    }
    &__desc {
      margin-top: 20px;
    }
    &__tel {
      margin: 40px 0 0 0;
      padding: 40px 0;
    }
  }
  .about {
    padding: 30px 0 15px;
    &__left {
      padding: 40px 15px;
    }
    &__text {
      margin-top: 15px;
    }
  }
  .team {
    padding: 15px 0 30px;
    &__btn {
      margin-top: 30px;
    }
    &__items {
      margin-top: 45px;
    }
    &__btn2 {
      margin-top: 45px;
    }
  }
  .opportunities {
    padding: 40px 0;
  }
  .services {
    .swiper-slide__btns {
      margin-top: 25px;
    }
  }
  .map {
    height: 350px;
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    padding: 30px 0;
    &-form {
      &__top {
        grid-template-columns: 1fr;
      }
      &__btn {
        width: 100%;
      }
    }
  }
}
